
@import url('https://fonts.googleapis.com/css?family=Roboto&display=swap');

body {
  margin: 0;
  padding: 0;
}
.demos{
  font-family: 'Roboto',  'Helvetica Neue Light', 'Open Sans', Helvetica;
  display: flex;

  nav{
    width: 350px;
    box-sizing: border-box;
    border-right: 1px solid #CBDCE4;

    .logo-container {
      padding: 20px 50px;
      border-bottom: 1px solid #CBDCE4;
    }

    ul{
      list-style-type: none;
      padding: 0;

      a {
        display: block;
        padding: 10px 0;
        text-decoration: none;
        .menu-button{
          .menu-button-inner{
            padding: 5px;
            width: 185px;
            margin-left: 40px;
            border-radius: 6px;
          }
        }

        &.active{
          color: #353C44;
          .menu-button{
            border-left: 4px solid #353C44;
            border-top: 4px solid white;
            border-bottom: 4px solid white;
          }
        }
      }
    }
  }

  main{
    width: calc(100% - 350px);
    box-sizing: border-box;

    .simulator-content{
      background: #ECF0F4;
      padding: 28px 80px 60px;

      h1{
        margin: 0 0 30px 20px;
        font-size: 22px;
      }
      .tc{
        border-radius: 12px;
        max-height: 600px;
        box-shadow: 5px 15px 30px rgba(0, 0, 0, 0.1);
      }
    }
    .code{
      padding: 60px 80px;
    }
  }
}